	module.exports = {"data":{"markdownRemark":{"html":"<p>I’m a big design fan and using the right font at the right time can make a huge difference in how your course design influences the emotional appeal you’re going for. They said it couldn’t be done, but with a little HTML and CSS, I figured out how to embed fonts in your published Lectora file! Here’s my first tutorial all about how to embed fonts into your Lectora Inspire published files using webfonts and CSS. I used <a href=\"https://www.google.com/fonts#UsePlace:use/Collection:Pacificohttp://\" title=\"Pacifico on Google Fonts\">Pacifico</a> and <a href=\"http://fontfabric.com/nexa-rust-free-font/\" title=\"Nexa Rust on Font-Fabric\">Nexa Rust</a> for the demo. Let me know if you have any questions, comments, or suggestions. [embed]<a href=\"http://vimeo.com/knanthony/lectorafontembed%5B/embed\">http://vimeo.com/knanthony/lectorafontembed[/embed</a>]</p>\n<h2>Pro Tip:</h2>\n<p>If you’re like me, you may have to test courses with your LMS on a secure server before being able to test on a production (or public) server. In those cases, you may find that the fonts don’t show up! To remedy this, simply change the font link to remove “http:”</p>\n<figure>\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 534px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 66.66666666666666%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Embedding fonts in Lectora\" title=\"\" src=\"/static/Lectora-font-embed-e7d39b631b7666556373921c6dcad086-6d242.png\" srcset=\"/static/Lectora-font-embed-e7d39b631b7666556373921c6dcad086-1cffb.png 203w,\n/static/Lectora-font-embed-e7d39b631b7666556373921c6dcad086-32715.png 405w,\n/static/Lectora-font-embed-e7d39b631b7666556373921c6dcad086-6d242.png 534w\" sizes=\"(max-width: 534px) 100vw, 534px\">\n    </span>\n  </span>\n  \n  <figcaption>Remove the http to ensure fonts are served in secure environments</figcaption>\n</figure>","frontmatter":{"date":"October 11, 2014","path":"/blog/embedding-fonts-in-lectora","title":"Embedding Fonts in Lectora","tags":["css","embed","fonts","Lectora","tutorial","typography"],"categories":["elearning","Tips","Weekend Project"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.768172888015717,"src":"/static/logoColor-c92143fb3723fcd5f12c0d0502759756-81d39.png","srcSet":"/static/logoColor-c92143fb3723fcd5f12c0d0502759756-9a8d1.png 203w,\n/static/logoColor-c92143fb3723fcd5f12c0d0502759756-c53f9.png 405w,\n/static/logoColor-c92143fb3723fcd5f12c0d0502759756-81d39.png 810w,\n/static/logoColor-c92143fb3723fcd5f12c0d0502759756-e81a9.png 900w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

